import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { getAuthHeaderConfig } from './backend_modules/services/UabsService';
import { setMockToken as _setMockToken, mockUsers } from './backend_modules/mockData/mockUsers.js';


export default function ViewExperiments() {
    const [experiments, setExperiments] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        // Inject mock token for testing
        _setMockToken("user1");

        const fetchExperiments = async () => {
            try {
                const config = await getAuthHeaderConfig();
                const response = await axios.get('/api/experiments', config);
                setExperiments(response.data);
            } catch (err) {
                console.error('Error fetching experiments:', err);
                setError(err.message || 'Failed to fetch experiments');
            } finally {
                setLoading(false);
            }
        };

        fetchExperiments();
    }, []);

    return (
        <div className="ui-wrapper">
            <div className="ui-header">Experiments Dashboard</div>
            <div className="ui-container">
                {loading ? (
                    <p>Loading experiments...</p>
                ) : error ? (
                    <p style={{ color: 'red' }}>Error: {error}</p>
                ) : (
                    <table className="ui-table">
                        <thead>
                        <tr>
                            <th>ID</th>
                            <th>Name</th>
                            <th>Encoder</th>
                            <th>Duration</th>
                            <th>Status</th>
                            <th>Output</th>
                            <th>Log</th>
                        </tr>
                        </thead>
                        <tbody>
                        {experiments.length === 0 ? (
                            <tr>
                                <td colSpan="7">No experiments available</td>
                            </tr>
                        ) : (
                            experiments.map((exp) => (
                                <tr key={exp.id}>
                                    <td>{exp.id}</td>
                                    <td>{exp.name}</td>
                                    <td>{exp.encoder}</td>
                                    <td>{exp.duration}</td>
                                    <td>{exp.status}</td>
                                    <td>
                                        {exp.outputLink ? (
                                            <a href={exp.outputLink} target="_blank" rel="noopener noreferrer">Download</a>
                                        ) : 'Pending'}
                                    </td>
                                    <td>
                                        {exp.logLink ? (
                                            <a href={exp.logLink} target="_blank" rel="noopener noreferrer">View Log</a>
                                        ) : 'N/A'}
                                    </td>
                                </tr>
                            ))
                        )}
                        </tbody>
                    </table>
                )}
            </div>
        </div>
    );
}
